import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import "./detail.scss";

function Detail(props) {
  let [알림, 알림변경] = useState(true);

  useEffect(() => {
    const 타이머 = setTimeout(() => {
      알림변경(false);
    }, 3000);

    // 페이지 벗어나게 되면
    return () => {
      clearTimeout(타이머);
    };
  }, []);

  const { id } = useParams();
  const 찾은상품 = props.shoes.find((상품) => {
    return 상품.id === Number(id);
  });
  const history = useHistory();

  const Box = styled.div`
    padding: 20px;
  `;
  const Title = styled.h3`
    font-size: 24px;
    font-weight: bold;
    color: ${(props) => props.color};
  `;

  const [inpData, inpData변경] = useState("");

  return (
    <div className="container">
      <input
        type="text"
        onChange={(e) => {
          inpData변경(e.target.value);
        }}
      />
      <div>{inpData}</div>

      <Box>
        <Title color={"red"}>Detail</Title>
        {알림 === true ? (
          <div className="my-alert2">
            <p>상품 마감 임박입니다!!</p>
          </div>
        ) : null}
      </Box>
      <div className="row">
        <div className="col-md-6">
          <img
            src={`https://codingapple1.github.io/shop/shoes${
              Number(id) + 1
            }.jpg`}
            width="100%"
            alt=""
          />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}</p>
          <Stock 재고={props.재고}></Stock>
          <button
            className="btn btn-danger"
            onClick={() => {
              props.재고변경([1, 2, 3]);
            }}
          >
            주문하기
          </button>
          <button
            className="btn btn-danger ml10"
            onClick={() => {
              history.goBack();
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}

function Stock(props) {
  return <p>{props.재고[0]}</p>;
}

export default Detail;
